import React, {useEffect, useRef, useState} from "react";
import {isIOS, isMobile} from "react-device-detect";
import {currentFilm} from "../../types/AudioPlayerTypes";
import loader from "../../images/loader.gif"
import './index.css'
import {AvailableLanguages} from "../AvailableLanguages/AvailableLanguages";
import {Loader} from "../Loader/Loader";

export const AudioPlayer = () => {

  const [audioUrl, setAudioUrl] = useState<string>('');
  const [currentFilm, setCurrentFilm] = useState<currentFilm>({
    available_languages: [],
    end_time_ms: 0,
    show_name: "",
    start_time_ms: 0
  });
  const [chosenLanguage, setChosenLanguage] = useState<string>('')
  const [canPlay, setCanPlay] = useState<boolean>(false)
  const [isLoading, setIsLoading] = useState<boolean>(false);
  const [isGoing, setIsGoing] = useState<boolean>(false);

  const desktopAudio = useRef<HTMLAudioElement | null>(null);
  const mobileAudio = useRef<HTMLAudioElement | null>(null);

  const buttonPlayClasses = `button-play ${canPlay && 'button-play_active'}`

  useEffect(() => {
    connectSocket();
    fetch('http://91.229.221.53/narrations')
      .then(response => {
        if (response.status === 204) {
          setIsGoing(false);
          return
        }
        return response.json();
      })
      .then((film: currentFilm) =>  {
        if (film) {
          handleFilmInfo(film);
        }

      })
      .catch(err => console.log(err))
  }, []);

  useEffect(() => {
    const now = Date.now();
    if (now > currentFilm.end_time_ms) {
      setIsGoing(false);
      setCanPlay(false);
      return
    }
    if (currentFilm && chosenLanguage) {
      setIsGoing(true);
      getAudioFile(chosenLanguage);
    }
  }, [chosenLanguage, currentFilm]);


  const startAudio = () => {
    let audio;
    if (isMobile && isIOS) {
      audio = mobileAudio.current;
    } else {
      audio = desktopAudio.current;
    }
    if (audio === null) {
      return
    } else {
      audio.currentTime = getCurrentTiming();
      audio.play()
        .then(() => {})
        .catch((error: any) => {console.log(error)})
    }

  };

  function getCurrentTiming() {
    const diff = new Date(Date.now() - currentFilm.start_time_ms);
    return  (diff.getMinutes() * 60 + diff.getSeconds())
  }

  function getAudioFile(lang: string) {
    setIsLoading(true);
    setAudioUrl('');
    fetch(`http://91.229.221.53/narrations/${lang}`)
      .then(response => {
        // @ts-ignore
        const reader = response.body.getReader();
        return new ReadableStream({
          start(controller) {
            return pump();
            // @ts-ignore
            function pump() {
              return reader.read().then(({ done, value }) => {
                if (done) {
                  controller.close();
                  return;
                }
                controller.enqueue(value);
                return pump();
              });
            }
          }
        })
      })
      .then(stream => new Response(stream))
      .then(response => response.blob())
      .then(blob => URL.createObjectURL(blob))
      .then(url => setAudioUrl(url))
      .catch(err => console.error(err))
      .finally(() => {
        setTimeout(() => {
          setIsLoading(false);
        }, 1000)
      })
  }

  function chooseLanguage(lang: string) {
    setCanPlay(false);
    setChosenLanguage(lang)
  }

  function canPlayHandler() {
    setTimeout(() => {
      setCanPlay(true)
    }, 1000)
  }

  function handleFilmInfo(film: currentFilm) {
    setChosenLanguage('');
    setIsGoing(true);
    setCurrentFilm(film);
    setCanPlay(false);
  }

  function connectSocket() {
    const socket = new WebSocket("ws://91.229.221.53/ws");
    socket.onmessage = (data) => {
      handleFilmInfo(JSON.parse(JSON.parse(data.data)));
    }
    socket.onopen = (data) => {
      //console.log('socket opened');
    }
  }

  return (
    <div className={'translation-main-container'}>
      <Loader isLoading={isLoading}/>
      {isGoing ?
        <>
          {(isMobile && isIOS) ?
            <audio ref={mobileAudio} onCanPlay={canPlayHandler} controls className={'audio-player'}>
              <source src={audioUrl} type="audio/mpeg"/>
              <source src={audioUrl} type="audio/ogg" />
              <source src={audioUrl} type="mp3" />
            </audio>
            :
            <audio
              className={'audio-player'}
              onCanPlay={canPlayHandler}
              ref={desktopAudio}
              src={audioUrl}
              controls
            />
          }

          <div className={'translation-main-container__info-container'}>
            <p>Now: {currentFilm.show_name}</p>
            <p>Available languages: </p>
            <AvailableLanguages languages={currentFilm.available_languages} chooseLanguage={chooseLanguage}/>
          </div>
          {canPlay && <button type="button" onClick={startAudio} className={buttonPlayClasses}>Play</button>}
        </> :
        <p>  </p>
      }

    </div>
  );
};
