import React from "react";
import './index.css'
import {ButtonAvailableLanguage} from "./ButtonAvailableLanguage/ButtonAvailableLanguage";

interface Iprops {
  languages: string[],
  chooseLanguage: (language: string) => void
}

export const AvailableLanguages = ({languages, chooseLanguage}: Iprops) => {


  return (
    <div className={'available-languages'}>
      {languages?.map((lang, index) =>
        <ButtonAvailableLanguage key={index} chooseLanguage={chooseLanguage} lang={lang}/>
      )}
    </div>
  )
}
