import './index.css';


interface Iprops {
  lang: string,
  chooseLanguage: (lang: string) => void
}

export const ButtonAvailableLanguage = ({lang, chooseLanguage}: Iprops) => {
  function handleClick() {
    chooseLanguage(lang);
  }
  return (
    <button onClick={handleClick} className={'available-languages__button'}>
      {lang}
    </button>
  )
}
