import React, {useEffect, useRef, useState} from 'react';
import './App.css';
import {Route, Switch} from "react-router-dom";
import {routes} from "../../utils/routes";
import {useAppDispatch} from "../../hooks/useDispatch";
import {closeAuthModal, login, logout, openAuthModal, setAuthModalVariant} from "../../store/slices/auth/authSlice";
import {selectAuthModalState, selectAuthStatus, userSelector} from "../../store/slices/auth/authSelectors";
import {useSelector} from "react-redux";
import {PageHeader, Button, Typography, Modal, Form, Input} from "antd";
import { io } from "socket.io-client";
import {isIOS, isMobile} from 'react-device-detect';
import {AudioPlayer} from "../AudioPlayer/AudioPlayer";

const { Text } = Typography;

function App() {
  const dispatch = useAppDispatch();
  const { email } = useSelector(userSelector);
  const { isLogged } = useSelector(selectAuthStatus);
  const { isOpen, variant } = useSelector(selectAuthModalState);

  const [audio, setAudio] = useState<any>(new Audio());

  function handleLoginClick() {
    dispatch(openAuthModal())
  }

  function  handleCloseModal() {
    dispatch(closeAuthModal());
  }

  function handleLogoutClick() {
    dispatch(logout())
  }

  function toggleLoginRegister() {
    dispatch(setAuthModalVariant())
  }

  const onFinish = (values: any) => {
    console.log('Success:', values);
  };

  const onFinishFailed = (errorInfo: any) => {
    console.log('Failed:', errorInfo);
  };

  return (
    <div className="app">
      <PageHeader
        title="Moscow planetarium tech"
        extra={[
          isLogged ?
            [
              <Text code key={1}>{email}</Text>,
              <Button key={2} onClick={handleLogoutClick} type="primary" ghost></Button>
            ]
            :
            <Button key={3} onClick={handleLoginClick} type="primary" ghost></Button>,

        ]}
        ghost={true}
      />
      <main className="main">
        <Modal title={''} visible={isOpen && variant === 'register'} footer={false} centered={true} onCancel={handleCloseModal}>
          <Form
            name="basic"
            size={"small"}
            labelCol={{ span: 8 }}
            wrapperCol={{ span: 16 }}
            initialValues={{ remember: true }}
            onFinish={onFinish}
            onFinishFailed={onFinishFailed}
            autoComplete="off"
          >
            <Form.Item
              label="Email"
              name="email"
              rules={[{ required: true, message: 'Email -  ' }, { type: 'email', message: ' email' }]}
            >
              <Input />
            </Form.Item>

            <Form.Item
              label=""
              name="first_name"
              rules={[{ required: true, message: ' -  ' }]}
            >
              <Input />
            </Form.Item>

            <Form.Item
              label=""
              name="last_name"
              rules={[{ required: true, message: ' -  ' }]}
            >
              <Input />
            </Form.Item>

            <Form.Item
              label=""
              name="position"
              rules={[{ required: true, message: ' -  ' }]}
            >
              <Input />
            </Form.Item>

            <Form.Item
              label=""
              name="phone_number"
              rules={[{ required: true, message: ' -  ' }]}
            >
              <Input />
            </Form.Item>

            <Form.Item
              label="Telegram"
              name="tg_username"
              rules={[{ required: true, message: ' -  ' }]}
            >
              <Input />
            </Form.Item>

            <Form.Item
              label=""
              name="password"
              rules={[{ required: true, message: ' -  ' }]}
            >
              <Input.Password />
            </Form.Item>

            <Form.Item
              name="confirm"
              label=" "
              dependencies={['password']}
              hasFeedback
              rules={[
                {
                  required: true,
                  message: ' !',
                },
                ({ getFieldValue }) => ({
                  validator(_, value) {
                    if (!value || getFieldValue('password') === value) {
                      return Promise.resolve();
                    }
                    return Promise.reject(new Error('  !'));
                  },
                }),
              ]}
            >
              <Input.Password />
            </Form.Item>

            <Form.Item wrapperCol={{ offset: 8, span: 16 }}>
              <Button type="primary" htmlType="submit">
                
              </Button>
            </Form.Item>
          </Form>

          <Form.Item wrapperCol={{ offset: 8, span: 16 }}>
            <Text type="secondary">  ?</Text>
            <Button  onClick={toggleLoginRegister} type="link"></Button>
          </Form.Item>
        </Modal>

        <Modal title="" visible={isOpen && variant === 'login'} footer={false} onCancel={handleCloseModal}>
          <Form
            name="basic"
            size={"small"}
            labelCol={{ span: 8 }}
            wrapperCol={{ span: 16 }}
            initialValues={{ remember: true }}
            onFinish={onFinish}
            onFinishFailed={onFinishFailed}
            autoComplete="off"
          >
            <Form.Item
              label="Email"
              name="email"
              rules={[{ required: true, message: 'Email -  ' }, { type: 'email', message: ' email' }]}
            >
              <Input />
            </Form.Item>

            <Form.Item
              label=""
              name="password"
              rules={[{ required: true, message: ' -  ' }]}
            >
              <Input.Password />
            </Form.Item>

            <Form.Item wrapperCol={{ offset: 8, span: 16 }}>
              <Button type="primary" htmlType="submit">
                
              </Button>
            </Form.Item>

            <Form.Item wrapperCol={{ offset: 8, span: 16 }}>
              <Text type="secondary">  ?</Text>
              <Button  onClick={toggleLoginRegister} type="link"></Button>
            </Form.Item>
          </Form>
        </Modal>

        <Switch>
          <Route path={routes.main} exact>
            <h2>Main page</h2>
          </Route>
          <Route path={routes.translation} exact>
            <AudioPlayer/>
          </Route>
        </Switch>
      </main>
    </div>
  );
}

export default App;
